<template>
    <div id="search_vue">
      <van-nav-bar
        title="搜索"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
      <van-search placeholder="请输入搜索关键词" @click="onSearch" @keydown.enter="onSearch" v-model="searchInput"/>
      <div class="history">
        <span>历史</span>
        <van-icon name="delete-o" @click="deleteHistory"></van-icon>
      </div>
      <div class="list">
        <div class="list-item" v-for="item in searchHistory" :key="item" @click="handleSearchClick(item)">{{item}}</div>
      </div>
      <hot-search :list="hotSearchList" class="animate__animated animate__bounceInUp"/>
    </div>
  </template>
<script>
import hotSearch from '@/components/hotSearch.vue'
import searchHistoryStorage from '@/util/searchHistoryStorage'
export default {
  name: 'search_vue',
  components: {
    hotSearch
  },
  data: () => {
    return {
      // 热门搜索数据
      hotSearchList: [
        { article: { article_title: 'Vue.js 教程' } },
        { article: { article_title: 'React.js 入门' } },
        { article: { article_title: 'JavaScript 高级教程' } },
        { article: { article_title: '前端开发技巧' } },
        { article: { article_title: 'Node.js 和 Express' } }
      ],
      searchHistory: searchHistoryStorage.getStorage(),
      searchInput: ''
    }
  },
  methods: {
    onSearch () {
      if (this.searchInput === '') {
        return
      }
      const index = this.searchHistory.indexOf(this.searchInput)
      if (index === -1) {
        this.searchHistory.unshift(this.searchInput)
      } else {
        const temp = this.searchHistory[index]
        this.searchHistory.splice(index, 1)
        this.searchHistory.unshift(temp)
      }
      searchHistoryStorage.setStorage(this.searchHistory)
      this.$router.push({
        path: '/searchResult',
        query: {
          keyword: this.searchInput
        }
      })
    },
    // 删除收索历史
    deleteHistory () {
      searchHistoryStorage.removeStorage()
      this.searchHistory = []
    },
    // 持久化搜索关键词
    handleSearchClick (item) {
      const index = this.searchHistory.indexOf(item)
      this.searchHistory.splice(index, 1)
      this.searchHistory.unshift(item)
      searchHistoryStorage.setStorage(this.searchHistory)
      this.$router.push({
        path: '/searchResult',
        query: {
          keyword: item
        }
      })
    }
  }
}
</script>
  <style scoped>
.history{
    position: relative;
    margin: 10px auto;
    background-color: #c37777;
  }
  .history span{
    position: absolute;
    left: 10px;
    top: 0;
  }
  .history .van-icon{
    position: absolute;
    right: 10px;
    top: 0;
  }
 .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
    margin-top: 40px;
  }
  .list-item {
    width: 20%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
  </style>
