<template>
    <div class="hot-search">
      <ul>
        <li
          v-for="(item, index) in list"
          :key="index"
          :class="['search-item', { 'highlight': index < 3 }]"
        >
          <span class="index">{{ index + 1 }}</span>
          <span class="title">{{ item.article.article_title }}</span>
        </li>
      </ul>
    </div>
  </template>

<script>
export default {
  name: 'HotSearch',
  props: {
    // 接收动态绑定的热门搜索 list
    list: {
      type: Array,
      required: true,
      validator (value) {
        // 确保 list 中每个项都包含 article_title
        return value.every(item => item.article && item.article.article_title)
      }
    }
  }
}
</script>

  <style scoped>
  .hot-search {
    padding: 15px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }

  ul {
    list-style: none;
    padding: 0;
  }

  .search-item {
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #ddd;
    font-size: 14px;
    color: #333;
  }

  .search-item:last-child {
    border-bottom: none;
  }

  .index {
    width: 30px;
    font-weight: bold;
    font-size: 16px;
  }

  .title {
    flex-grow: 1;
    margin-left: 10px;
    font-size: 14px;
  }

  .highlight {
    color: #fc5185;
    font-weight: bold;
  }

  .search-item:nth-child(2).highlight {
    color: #ff6f61; /* 第二个用不同颜色 */
  }

  .search-item:nth-child(3).highlight {
    color: #ffbb33; /* 第三个用不同颜色 */
  }
  </style>
